/** 常用色值 **/
/** 常用字体大小 **/
.header {
  background-color: #fff;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
}
.header .header-top .logo {
  line-height: 60px;
  text-align: left;
  font-size: 18px;
}
.header .header-top .logo img {
  height: 40px;
  vertical-align: middle;
}
.header .header-top .logo span {
  margin-left: 10px;
}
.header .header-top a {
  margin-left: 20px;
  color: #f9c700;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 1px solid #f9c700;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
  font-size: 18px;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  top: 38px;
  left: 116px;
  border-top: 9px solid #fff;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
  font-size: 14px;
}
.header .breadcrumb .weather .date {
  margin-left: 10px;
}
.header .breadcrumb .weather .weather-image img {
  height: 15px;
}
.header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
  vertical-align: middle;
}
.simple-page .header-top {
  background: #1890ff;
  color: #fff;
}
.simple-page .ant-form,
.simple-page .ant-col-12,
.simple-page .weather {
  display: none;
}
