body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** 常用色值 **/
/** 常用字体大小 **/
.home_wrapper {
  background-color: #fff;
  height: calc(72vh);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.nav-left .logo {
  line-height: 90px;
  padding-left: 20px;
  background-color: #001529;
}
.nav-left .logo img {
  height: 35px;
}
.nav-left .logo h1 {
  color: #fff;
  font-size: 20px;
  display: inline-block;
  margin: 0 0 0 10px;
  vertical-align: middle;
  padding-left: 10px;
}

/** 常用色值 **/
/** 常用字体大小 **/
.header {
  background-color: #fff;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
}
.header .header-top .logo {
  line-height: 60px;
  text-align: left;
  font-size: 18px;
}
.header .header-top .logo img {
  height: 40px;
  vertical-align: middle;
}
.header .header-top .logo span {
  margin-left: 10px;
}
.header .header-top a {
  margin-left: 20px;
  color: #f9c700;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 1px solid #f9c700;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
  font-size: 18px;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  top: 38px;
  left: 116px;
  border-top: 9px solid #fff;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
  font-size: 14px;
}
.header .breadcrumb .weather .date {
  margin-left: 10px;
}
.header .breadcrumb .weather .weather-image img {
  height: 15px;
}
.header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
  vertical-align: middle;
}
.simple-page .header-top {
  background: #1890ff;
  color: #fff;
}
.simple-page .ant-form,
.simple-page .ant-col-12,
.simple-page .weather {
  display: none;
}

/** 常用色值 **/
/** 常用字体大小 **/
.footer {
  height: 100px;
  padding: 40px;
  text-align: center;
  color: #d7d7d7;
}

/** 常用色值 **/
/** 常用字体大小 **/
/** load **/
.ajax-loading {
  display: none;
}
.ajax-loading .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 40px;
  height: 80px;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 6px;
  text-align: center;
  z-index: 9999;
  font-size: 16px;
  color: #fff;
}
.ajax-loading .loading img {
  width: 32px;
  vertical-align: middle;
}
.ajax-loading .loading span {
  margin-left: 12px;
}
.ajax-loading .overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9998;
  background: #ffffff;
  opacity: 0.1;
}
/****/
ul,
li {
  list-style: none;
}
.clearfix::after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}
.container {
  display: flex;
}
.container .nav-left {
  background-color: #001529;
  color: #ffffff;
  height: calc(100vh);
}
.container .main {
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto;
}
.container .content {
  position: relative;
  padding: 20px;
}
.content-wrap {
  background-color: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;
}
.content-wrap .ant-table-wrapper {
  margin-left: -1px;
  margin-right: -2px;
}
.operate-wrap button {
  margin-right: 10px;
}

.card-wrap button {
  margin-right: 10px;
}

/* ui */
.card-wrap {
  margin-bottom: 10px;
}
.card-wrap button {
  margin-right: 20px;
}
.btn-group button {
  margin-right: 0;
}
/* modals */
/* use css to set position of modal */
.vertical-center-modal {
  text-align: center;
  white-space: nowrap;
}
.vertical-center-modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  width: 0;
}
.vertical-center-modal .ant-modal {
  display: inline-block;
  vertical-align: middle;
  top: 0;
  text-align: left;
}
