.detail-items .detail-form li {
  clear: both;
  margin: 20px 0;
  line-height: 20px;
  font-size: 15px;
  color: #333;
}
.detail-items .detail-form li::after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}
.detail-items .detail-form-left {
  float: left;
  width: 164px;
  text-align: right;
  color: #999;
}
.detail-items .detail-form-content {
  padding-left: 194px;
}
.order-map {
  height: 450px;
  margin: 25px -31px 0;
}

/** 常用色值 **/
/** 常用字体大小 **/
.ant-table-thead > tr > th,
.ant-table-tbody > tr > td {
  padding: 14px 6px;
  text-align: center;
}
.ant-table .ant-table-selection-column {
  min-width: 42px!important;
  width: 42px!important;
}
.ant-table .text-center {
  text-align: center;
}
.ant-table .text-left {
  text-align: left;
}
.ant-table.ant-table-middle-thead > tr > th,
.ant-table.ant-table-middle-tbody > tr > td {
  padding: 10px 6px;
}
.ant-table.ant-table-small-thead > tr > th,
.ant-table.ant-table-small-tbody > tr > td {
  padding: 8px 6px;
}
.ant-table-pagination {
  padding: 0 20px;
}

